    padding: 25px 25px 80px;
  }
}

.about__image {
  max-width: max-content;
}
@media (max-width: 767px) {
  .about__image {
    display: none;
  }
}

.about__text-wrapper {
  margin-left: 100px;
}
.about__text-wrapper h2 {
  margin: 0 0 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #041827;
}
@media (max-width: 767px) {
  .about__text-wrapper h2 {
    font-size: 28px;
    line-height: 39px;
  }
}
.about__text-wrapper p {
  margin: 0 0 20px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
}
@media (max-width: 767px) {
  .about__text-wrapper p {
    font-size: 14px;
    line-height: 19px;
  }
}
@media (max-width: 1023px) {
  .about__text-wrapper {
    margin-left: 29px;
