.about__description--hidden {
  display: none;
}

.about__button {
  width: 180px;
  padding: 14px 12px;
  border-style: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
