  border: 1px solid #00b2ff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .about__button {
    padding: 8px 12px;
  }
}

.main-footer {
  background-color: #041827;
}

.main-footer__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 1320px;
  margin: 0 auto;
  padding: 50px 60px 55px 40px;
}
@media (max-width: 1023px) {
  .main-footer__container {
    padding: 50px 50px 65px;
  }
}
@media (max-width: 767px) {
  .main-footer__container {
    display: block;
    padding: 35px 25px;
  }
}

@media (max-width: 1023px) {
  .main-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    flex-direction: column;
  }
}

.main-footer__logo {
  margin: 0 10px 25px 0;
}
@media (max-width: 1023px) {
  .main-footer__logo {
    margin: 0;
  }
}

.main-footer__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 205px;
  gap: 15px;
}

.social__link {
  display: flex;
  padding: 5px;
}

@media (max-width: 767px) {
  .footer-navigation {
    position: relative;
  }
  .footer-navigation::before {
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
    position: absolute;
    left: -25px;
  }
  .footer-navigation::after {
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
    position: absolute;
    left: -25px;
  }
}

.footer-navigation__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 150px;
  min-width: 470px;
  gap: 10px 30px;
}
@media (max-width: 1023px) {
  .footer-navigation__list {
    max-height: min-content;
    min-width: min-content;
  }
}
@media (max-width: 767px) {
  .footer-navigation__list {
    display: none;
  }
}

.footer-navigation__title {
  margin: 0 0 20px;
}
@media (max-width: 767px) {
  .footer-navigation__title {
    position: relative;
    width: 100%;
    padding: 20px 15px 20px 0;
    margin: 0;
  }
  .footer-navigation__title::before {
    content: "";
    position: absolute;
    right: 0;
    top: 28px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .footer-navigation__title::after {
    content: "";
    position: absolute;
    right: 0;
    top: 28px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    transform: rotate(90deg);
  }
}

.footer-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}

@media (max-width: 1150px) {
  .footer-contacts {
    width: 174px;
  }
}
@media (max-width: 1023px) {
  .footer-contacts {
    min-width: 308px;
  }
}
@media (max-width: 767px) {
  .footer-contacts {
    position: relative;
    width: 100%;
    min-width: 100%;
  }
  .footer-contacts::before {
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
    position: absolute;
    left: -25px;
  }
  .footer-contacts::after {
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
    position: absolute;
    left: -25px;
  }
}

.footer-contacts__title {
  margin: 0 0 20px;
}
@media (max-width: 767px) {
  .footer-contacts__title {
    width: 100%;
    padding: 20px 15px 20px 0;
    margin: 0;
  }
  .footer-contacts__title::before {
    content: "";
    position: absolute;
    right: 0;
    top: 28px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .footer-contacts__title::after {
    content: "";
    position: absolute;
    right: 0;
    top: 28px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    transform: rotate(90deg);
  }
}

.footer-contacts__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer-contacts__list {
    display: none;
  }
}

